@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100..900;1,100..900&family=Montserrat:ital,wght@0,800;1,800&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

* {
  box-sizing: border-box;
}

.phone-nav-container{
  display: none;
}

.navbar-container,
.black-navbar-container
{
  font-family: "Montserrat", serif;
  font-optical-sizing: auto;
  position: absolute;
  z-index: 10;
  width: 100%;
  display: flex;
  margin-top: 1rem;
  font-weight: 800;
  font-style: normal;
  color: rgb(94, 94, 94);
  display: flex;
  justify-content: center;
}
.black-navbar-container{
  padding: 0;
  margin: 0;
  color: black;
  position: static;
  border-bottom: 1px solid rgba(0, 0, 0, 0.11);
  padding: 1rem 0 1rem 0;
}
.nav-bar
{
  width: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.navbar-container ul,
.black-navbar-container ul
{
  display: flex;
  flex-direction: row;
  gap: 2rem;
}

.nav-bar li{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  white-space: nowrap;
  font-size: 1.1rem;
  cursor: pointer;
  transition: ease-in-out 0.5s;
}
.shopping-cart-container {
  margin-left: 3rem;
  border-radius: 50%;
  padding: 0.4rem;
  cursor: pointer;
  transition: ease 0.5s;
}

.shopping-cart {
  color: rgba(240, 248, 255, 0.699);
  font-size: 1.4rem;
}

li a {
  text-decoration: none;
  color: inherit;
}

.navbar-container li:hover{
  color: rgba(255, 255, 255, 0.5);
}

.black-navbar-container li:hover{
  color: rgba(126, 102, 102, 0.788);
}

.logo-container {
  flex-shrink: 0;
  align-self: center;
  height: 2.5rem;
}

.logo {
  width: 11rem !important;
  height: 3rem !important;
  
}

.border-bottom {
  border-bottom: 0.1rem solid rgba(0, 0, 0, 0.308);
}

.pos-r {
  margin: 0;
  position: relative;
}

/* Responsive Styles */
@media (max-width: 990px) {
  .nav-bar ul{
    margin-left: 3rem;
    gap: 0.3rem;
  }
  .nav-bar ul{
    width: 80%;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(4, 1fr);
    justify-items: center;
  }
  ul li:nth-child(1) {
    grid-row: 1;
    grid-column: 1;  /* First column */
  }
  
  ul li:nth-child(2) {
    grid-row: 2;
    grid-column: 1;  /* First column */
  }
  
  ul li:nth-child(3) {
    grid-row: 3;
    grid-column: 1;  /* First column */
  }

  ul li:nth-child(4) {
    grid-row: 2;
    grid-column: 2;
  }
  ul li:nth-child(5) {
    grid-row: 1;
    grid-column: 3;
  }
  ul li:nth-child(6) {
    grid-row: 2;
    grid-column: 3;
  }
  ul li:nth-child(7) {
    grid-row: 3;
    grid-column: 3;
  }
  ul li:nth-child(8) {
    position: absolute;
    right: 2rem;
    top: 3.2rem;
  }
}

/* General styles */
.phone-nav-container {
  position: relative;
  padding: 10px;
  width: 100%;
}

/* Phone navigation bar */
.phone-nav {
  display: grid;
  align-items: center;
  width: 100%;
  padding: 10px 20px;

}

@keyframes  anime{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

.phone-nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  animation: ease-in 1s anime;
}
.phone-nav ul li{
  border: 1px;
}

/* Menu button */
.menu-icon button {
  background: none;
  border: none;
  font-size: 24px;
  color: white;
  cursor: pointer;
}
.logo-container{
  
}
/* Dropdown menu */
.menu-icon div {
  animation: ease-in 0.3s anime;  
  position: absolute;
  top: 60px;
  left: 0;
  width: 200px;
  background: #444444c4;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  display: none;
  transition: opacity 0.3s ease-in-out;
}

.menu-icon div ul {
  display: flex;
  flex-direction: column;
  gap: 0;
}

.menu-icon div ul li {
  padding: 1rem;
  text-align: center;
  width: 100%;
  color: white;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
}

.menu-icon div ul li:hover {
  background: #555;
}

/* Show menu when open */
.menu-icon:hover div {
  display: block;
}

/* Logo styling */
.logo-container .logo {
  height: 40px;
}

/* Shopping cart */
.shop-cart-container {
  font-size: 20px;
  color: rgba(255, 255, 255, 0.589);
  cursor: pointer;
}
@media (max-width: 580px) {
  .phone-nav-container{
    display: block;
  }
  .nav-bar{
    display: none;
  }
}