@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

#main-img{
  
}
@keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(70px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }


  @keyframes zoomOut {
    from {
      transform: scale(1.3);
      opacity: 0.2;
    }
    to {
      transform: scale(1);
      opacity: 1;
    }
  }
.products-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.7rem;
}

.products-section {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-top: 3rem;
    width: 100%;
    max-width: 1200px;
    opacity: 0;
}
.product-card {
  cursor: pointer;
}

.product-card img {
    height: auto !important;
    width: 100%;
    object-fit: cover;
}

.product-name {
    font-family: "Montserrat", serif;
    font-optical-sizing: auto;
    font-weight: 800;
    font-style: normal;
    font-size: 1.5rem;
    margin-top: 1rem;
}

.product-description {
    font-size: 1rem;
    color: #666;
    margin-top: 0.5rem;
}

.shop-now-button {
    border: none;
    font-size: 0.9rem;
    margin-top: 1rem;
    padding: 0.6rem 1.3rem;
    background-color: #000000;
    color: white;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.3s ease;
    text-decoration: none;
    display: block;
    width: fit-content;
}
.product-card a{
  text-decoration: none;
  color: inherit;
}
/* Responsive Design */
@media (max-width: 768px) {
    .products-section {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
        gap: 1.5rem;
    }

    .product-card img {
        height: 14rem;
    }

    .product-name {
        font-size: 1.3rem;
    }

    .product-description {
        font-size: 0.9rem;
    }

    .shop-now-button {
        font-size: 0.8rem;
        padding: 0.5rem 1rem;
    }
}
@media (max-width: 480px) {
    .products-section {
        grid-template-columns: 1fr;
    }
    .product-card {
        max-width: 100%;
    }
    .product-card img {
        height: 12rem;
    }
    .product-name {
        font-size: 1.2rem;
    }
    .product-description {
        font-size: 0.85rem;
    }
}



/* Best Sellers Section */
.bs-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 5rem;
    margin-top: 3rem;
    width: 100%;
    padding: 1rem;
    
}

.bs-section {
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
}

.bs-products {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
    width: 100%;
    max-width: 1200px;
    padding: 1rem;
    justify-content: center;
    opacity: 0;
}

.bs-card {
    width: 100%;
    height: auto !important;
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    padding: 1rem;
}

.bs-card:hover {
    transform: translateY(-10px);
}

.bs-card img {
    width: 100%;
    object-fit: cover;
}

.bs-name {
    padding: 0.6rem;
    color: black;
    font-size: 1.5rem;
    font-family: "Montserrat", sans-serif;
    font-weight: bold;
    margin-top: 1rem;
    display: inline-block;
}
