@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400..900&family=Exo+2:ital,wght@0,100..900;1,100..900&family=Montserrat:ital,wght@0,800;1,800&family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');
/* General Styling */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(70px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInLeftToRight {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRightToLeft {
  from {
    opacity: 0;
    transform: translateX(100px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes opacityAnimation{
  from {
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
  .pr-wrapper{
   width: 100%;
   display: flex;
   justify-content: center;
  }
  .product-container{
    width: 75%;
    gap: 1rem;
    margin-top: 4rem;
    display: flex;
    flex-direction: row;
  }
  
  .product-pictures-part{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    width: 50%;
    opacity: 0;
  }
  .y-carousel {
    gap: 2rem;
    display: flex;
    width: auto;
    flex-direction: row;
  }
  .thumbs-wrapper{
    display: flex;
  }
  .thumbs{
    height: 29rem;
    overflow-y: scroll;
    scrollbar-width: none;
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
  }
  .thumbs::-webkit-scrollbar{
    display: none;
  }
  .thumbs img{
    border-radius: 6%;
    width: 3.5rem;
    height: auto;
  }
  .thumbs img.darkened {
    filter: brightness(85%);
  }
  .main-image{
    width: 70%;
    height: auto;
  }
  .main-image img {
    width: 100%;
    border-radius: 10px;
    transition: transform 0.4s ease-in-out, opacity 0.4s;
  }


  @keyframes slide-in {
    from {
      transform: translateX(-100%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
  
  /* Product Details */
  .product-details {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 50%;
    opacity: 0;
  }
  
  .new-badge {
    background: #aaa9a983;
    color: black;
    padding: 0.1rem 0.3rem;
    border-radius: 5px;
    font-size: 0.5rem;
    font-weight: bold;
    align-self: flex-start;
  }
  
  .product-title {
    font-size: 1.8rem;
    font-weight: bold;
    color: #333;
  }
  
  .product-price {
    font-size: 1.5rem;
    font-weight: bold;
  }
  
  /* Size Selection */
  .size-selection h6 {
    font-weight: 300;
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }
  
  .size-options {
    display: flex;
    gap: 0.1rem;
  }
  
  .size {
    font-size: 0.7rem;
    background: #e0e0e0;
    padding: 0.3rem 0.7rem;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
  }
  .all-prod-div img:hover{
    border: 1px solid rgba(0, 0, 0, 0.705);
  }
  .all-prod-div .bordred{
    border: 1px solid rgba(0, 0, 0, 0.705);
  }
  .size:hover {
    background: #bdbdbd;
  }
  
  /* Buttons */
  .button-group {
    display: flex;
    gap: 1rem;
    margin-top: 1rem;
  }
  
  .add-to-cart, .favorite {
    border: none;
    padding: 0.6rem 1.9rem;
    border-radius: 5px;
    font-size: 0.7rem;
    cursor: pointer;
    transition: background 0.3s;
  }
  
  .add-to-cart {
    background: black   ;
    color: white;
  }
  
  .add-to-cart:hover {
    background: #e04848;
  }
  
  .favorite {
    background: #e0e0e0;
    color: #333;
  }
  
  .favorite:hover {
    background: #bdbdbd;
  }
  .more-products{
    margin-top: 2.5rem;
  }
  .more-products h1{
    text-align: center;
  }
  .more-products-images-container{
    margin: 3rem 0 5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .more-products-images{
    place-items: center;
    width: 80%;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
  .more-products-card{
    transition: transform 0.2s ease-in-out;
    font-family: "Montserrat", serif;
    font-optical-sizing: auto;
    width: 18rem;
  }
  .more-products-card:nth-child(3){
    width: 22rem;
    margin-bottom: 2rem;
  }
  .more-products-card:nth-child(3) h3{
    margin-top: -20px;
  }

  .more-products-card h3{
    font-size: 1.5rem;
    font-weight: 300;
    margin-top: 1rem;
    text-align: center;
  }
  .more-products-card p{
    margin-top: 1rem;
    text-align: center;
  }

  .more-products-images img{
    width: 100%;
  }
  .more-products-card:hover{
    transform: scale(1.1);
  }


  /* Responsive Design */
  @media (max-width: 768px) {
    .product-container {
      flex-direction: column;
      align-items: center;
      width: 100%;
      padding: 1.5rem;
      gap: 2rem;
    }
    .thumbs{
      height: 22rem;
    }
    .product-pictures-part{
      width: 90%;
    }
    .product-details {
      width: 100%;
    }
  }

  .black-nav-bar{
    color: black;
  }


  @media (max-width: 768px) {
    .product-container {
      flex-direction: column;
      align-items: center;
      width: 100%;
      padding: 1.5rem;
    }
    .thumbnail-list{
      flex-direction: row;
      gap: 0.5rem;
    }
    .product-pictures-part{
      width: 90%;
      display: flex;
      flex-direction: column;
    }
    .product-details {
      width: 100%;
    }
  }

  .black-nav-bar{
    color: black;
  }
  

  