@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400..900&family=Exo+2:ital,wght@0,100..900;1,100..900&family=Montserrat:ital,wght@0,800;1,800&family=Open+Sans:ital,wght@0,300..800;1,300..800&family=Oswald:wght@200..700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Smooch+Sans:wght@100..900&display=swap');
/* Cart Overlay */
.cart-overlay {
    position: fixed;
    top: 0;
    right: 0;
    width: 400px;
    height: 100vh;
    background: white;
    box-shadow: -5px 0 10px rgba(0, 0, 0, 0.2);
    z-index: 1000;
    display: flex;
    flex-direction: column;
  }
  
  /* Cart Container */
  .shop-cart-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;
  }
  .shop-cart-container h1{
    color: black;
  }
  /* Cart Header */
  .shop-cart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1rem 2rem 1rem;
    border-bottom: 1.5px solid #0000002a;
  }
  .shop-cart-header h1 {
    font-family: "Montserrat", sans-serif;
    font-optical-sizing: auto;
    font-weight: 800;
    font-style: normal;
    font-size: 1.5rem;
  }
  .cart-exit-btn{
    border: none;
    background-color: white;
    cursor: pointer;
  }
  .cart-products{
    overflow-y: scroll;
    height: 65%;
    padding: 2rem 1rem 2rem 1rem ;
    border: 1px solid;
    scrollbar-width: none;
  }
  .cart-products::-webkit-scrollbar {
    display: none;
}
  .cart-product{
    
    gap: 1rem;
    display: flex;
    flex-direction: row;
  }
  .cart-product-title{
    color: black;
    font-size: 1rem;
    font-weight: lighter;
    margin-bottom: 1rem;
  }
  .cart-product img{
    width: 7rem;
  }
  .cart-product-price{
    color: black;
  }
  .cart-product-counter{
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 0.5rem;
    max-width: fit-content;
    border: 1px solid rgba(0, 0, 0, 0.281);
  }
  .cart-product-counter p{
    padding: 0.4rem;
  }

  #counter-minus:hover,
  #counter-plus:hover{
    background-color: rgba(8, 5, 5, 0.212);
  }
  
  .cart-total{
    padding: 2rem;
    color: black;
    border-top: 1.5px solid rgba(0, 0, 0, 0.281);
    display: flex;
    justify-content: space-between;
    
  }
  .cart-total h3{
    font-family: "Roboto", sans-serif;
    font-optical-sizing: auto;
    font-weight: 800;
    font-style: normal;
    letter-spacing: 3px;
  }
  .cart-total p{
    letter-spacing: 2px;
    font-size: 1.5rem;
    font-family: "Smooch Sans", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;
    font-weight: 500;
  }
  .check-out-button-container{
    width: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
  }
  .check-out-button{
    border: none;
    font-weight: bold;
    font-size: 0.9rem;
    background-color: black;
    color: white;
    width: 70%;
    padding: 0.75rem 0 0.75rem 0;
    font-family: "Roboto", sans-serif;
    font-optical-sizing: auto;
    font-weight: 800;
    font-style: normal;
    cursor: pointer;
  }
  .check-out-button:hover{
    background-color: rgba(0, 0, 0, 0.815);
  }
  .empty-cart{
    text-align: center;
    color: rgba(0, 0, 0, 0.329);
  }