/* Modern Checkout Page Styles */

.checkout-page {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
  padding: 40px;
  background-color: #f8f9fa;
  font-family: 'Inter', sans-serif; /* Modern font family */
}

/* Left Side: User Information */
.checkout-form-section {
  flex: 1;
  background: #ffffff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  animation: fadeInLeft 0.6s ease-in-out;
}

.checkout-form h2 {
  font-size: 28px; /* Adjusted font size for better readability */
  margin-bottom: 20px;
  color: #333;
  letter-spacing: 1px; /* Added letter spacing for a modern touch */
}

.checkout-form fieldset {
  border: none;
  margin-bottom: 20px;
}

.checkout-form label {
  display: block;
  font-weight: 600;
  margin: 5px 0;
  font-size: 16px; /* Increased font size for labels */
  letter-spacing: 0.5px; /* Slight letter spacing for better readability */
}

.checkout-form input, .checkout-form select {
  width: 100%;
  padding: 12px; /* Increased padding for better input area */
  border-radius: 6px;
  border: 1px solid #ccc;
  margin-bottom: 12px; /* Adjusted margin for better spacing */
  font-size: 16px; /* Increased font size for inputs */
  letter-spacing: 0.5px; /* Added letter spacing */
}

.input-group {
  display: flex;
  gap: 12px; /* Increased gap between inputs */
}

.checkout-button {
  width: 100%;
  padding: 14px; /* Increased padding for better button size */
  font-size: 18px; /* Increased font size */
  background: linear-gradient(135deg, #ff7eb3, #ff758c);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease;
  letter-spacing: 1px; /* Added letter spacing */
}

.checkout-button:hover {
  background: linear-gradient(135deg, #ff758c, #ff7eb3);
}

/* Right Side: Order Summary */
.order-summary {
  width: 350px;
  background: #ffffff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 20px;
  animation: fadeInRight 0.6s ease-in-out;
}

.order-summary h3 {
  font-size: 24px; /* Increased font size for order summary heading */
  margin-bottom: 15px;
  color: #333;
  letter-spacing: 1px; /* Added letter spacing */
}

.order-items {
  max-height: 300px;
  overflow-y: auto;
  padding-right: 10px;
}

.count-badge {
  position: absolute;
  top: 0px;
  right: -5px;
  background-color: rgba(0, 0, 0, 0.63);
  color: white;
  font-size: 12px;
  font-weight: bold;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.order-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 12px 0; /* Adjusted padding for better spacing */
  border-bottom: 1px solid #ddd;
}

.order-item-img {
  width: 55px; /* Slightly larger image for better display */
  height: 55px;
  object-fit: cover;
  border-radius: 6px;
}

.order-item-details p {
  margin: 0;
  font-size: 16px; /* Increased font size */
  letter-spacing: 0.5px; /* Added letter spacing */
}

.discount {
  color: red;
}

.order-total {
  margin-top: 15px;
}

.total {
  font-size: 20px; /* Increased font size for total */
  font-weight: bold;
  letter-spacing: 1px; /* Added letter spacing */
}

/* Animations */
@keyframes fadeInLeft {
  from { opacity: 0; transform: translateX(-30px); }
  to { opacity: 1; transform: translateX(0); }
}

@keyframes fadeInRight {
  from { opacity: 0; transform: translateX(30px); }
  to { opacity: 1; transform: translateX(0); }
}
