@keyframes zoomOut {
  from {
    transform: scale(1.3);
    opacity: 0.2;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
.best-sellers-container {
    position: relative;
    margin-top: 5rem;
    opacity: 0;
  }
  
  .best-sellers-hero-img {
    width: 100%;
    height: auto;
    object-fit: cover;
}
  
  .best-sellers-text {
    position: absolute;
    left: 5%;
    top: 31%;
    display: flex;
    flex-direction: column;
    color: white;
    font-size: 4rem;
  }

  .best-sellers-desc{
  gap: 1rem;
  display: flex;
  flex-direction: row;
}
  .word {
    font-size: 4vw; /* Make sure words have the same size */
  }
  
  .vibrant {
    font-family: 'Arial', sans-serif;
    color: #2AB493; /* Turquoise */
  }
  
  .refreshing {
    font-family: 'Times New Roman', serif;
    color: #b43b25; /* Tomato red */
  }
  
  .iconic {
    font-family: 'Courier New', monospace;
    color: white;
    font-weight: bold;
  }

  .shop-btn {
    margin-top: 1.5rem;
    font-size: 1.3rem;
    font-weight: bold;
    color: rgb(255, 255, 255);
    background-color: rgba(240, 248, 255, 0.233);
    border: 2px solid #ffffff;
    padding: 10px 25px;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    align-self: flex-start; /* Aligns the button to the left */
  }
  
  .shop-btn:hover {
    background: white;
    color: black;
  }
   
  @media (max-width: 768px) {
    .best-sellers-text {
      position: absolute;
      top: 5%;
      display: flex;
      flex-direction: row;
      color: white;
      align-items: center;
      justify-content: center;
      gap: 1rem;
      margin: 0;
    }
    .best-sellers-desc{
      gap: 0.7rem;
      display: flex;
      flex-direction: row;
    }
    .shop-btn{
      font-size: 2.8vw;
      margin: 0;
      padding: 6px 10px;
      white-space: nowrap;
    }

    .word {
      font-size: 5vw; /* Make sure words have the same size */
    }
    .best-sellers-hero-img {
        height: 60vh; /* 60% of the screen height */
        object-position: center right; /* Focus on the right side */
    }

}