/* General Footer Styling */
.footer {
    background-color: #f7f7f7;
    color: #333;
    padding: 2rem 1rem;
    border-top: 2px solid rgba(0, 0, 0, 0.1);
  }
  
  /* Footer Container */
  .footer-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4rem;
    text-align: left;
  }
  
  /* Footer Sections */
  .footer-section {
    flex: 1;
    min-width: 200px;
    max-width: 250px;
  }
  
  .footer-section h4 {
    font-size: 1rem;
    color: #444;
    margin-bottom: 1.2rem;
  }
  
  .footer-section ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
  }
  
  .footer-section ul a {
    font-size: 0.95rem;
    text-decoration: none;
    color: #666;
    transition: color 0.3s ease;
  }
  
  .footer-section ul a:hover {
    color: #000;
  }
  
  /* Footer Bottom */
  .footer-bottom {
    margin-top: 2rem;
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #e0e0e0;
  }
  
  .footer-bottom p {
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
  }
  
  /* Footer Icons */
  .footer-icons {
    display: flex;
    gap: 1rem;
  }
  
  .footer-icons a {
    font-size: 1.5rem;
    text-decoration: none;
    color: #555;
    transition: color 0.3s ease;
  }
  
  .footer-icons a:hover {
    color: #000;
  }
  
  /* ===== Responsive Design ===== */
  
  /* Tablets & Small Screens */
  @media (max-width: 768px) {
    .footer-container {
      gap: 2rem;
    }
  
    .footer-section h4 {
      font-size: 0.9rem;
    }
  
    .footer-section ul a {
      font-size: 0.85rem;
    }
  }
  
  /* Mobile View */
  @media (max-width: 550px) {
    .footer-container {
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 1.5rem;
    }
  
    .footer-section {
      max-width: 100%;
    }
  
    .footer-section ul {
      align-items: center;
    }
  
    .footer-bottom {
      padding: 1rem;
    }
  }
  