@keyframes zoomOut {
  from {
    transform: scale(1.3);
    opacity: 0.8;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
#phone-image{
  display: none;
}
.heroImg {
  width: 100%;
  height: 45rem;
  overflow: hidden;
  position: relative;;
}

.heroImg img {
  animation: zoomOut 1.5s ease-out;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  position: absolute;
}


/* Hero description container */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px); /* Starts slightly lower */
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.hero-desc {
  position: absolute;
  top: 40%;
  left: 10%;
  transform: translateY(-50%);
  font-size: 3rem;
  font-weight: bold;
  text-align: left;
  display: flex;
  flex-direction: column;
  gap: 15px;
  opacity: 0; /* Initially hidden */
  animation: fadeIn 1s ease-out 1.2s forwards; /* Delay before appearing */
}
.gradient-text {
  background: linear-gradient(to right, rgba(255, 0, 0, 0.637) 5%, rgb(156, 156, 156));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  display: inline-block;
}
.hero-desc p{
  width: 15rem;
  font-family: 'Inter', sans-serif;
}
/* Shop Now button */
.shop-now-btn {
  font-size: 1.1rem;
  font-weight: bold;
  color: white;
  background: transparent;
  border: 2px solid rgba(255, 255, 255, 0.397);
  padding: 10px 25px;
  border-radius: 50px;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  align-self: flex-start; /* Aligns the button to the left */
}

.shop-now-btn:hover {
  background: rgba(141, 138, 138, 0.76);
  color: rgb(255, 255, 255);
}

@media (max-width: 580px) {
  .hero-desc{
    top: auto;
    bottom: 5%;
  }
  .gradient-text {
    background: linear-gradient(to right, red 5%, rgb(156, 156, 156));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: inline-block;
  }
}

@media screen and (max-width: 580px) {   
  #phone-image{
    display: block;
  }
  #desktop-image{
    display: none;
  }
}