@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;600&display=swap');

.contact-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  font-family: 'Poppins', sans-serif;
  padding: 2rem 0;
}

.contact-container input, 
.contact-container textarea {
  border: 1px solid rgba(0, 0, 0, 0.1);
  font-family: inherit;
  font-size: 1rem;
  padding: 0.8rem;
  border-radius: 5px;
}

.contact-container h6 {
  font-weight: 400;
  margin-bottom: 0.8rem;
  font-size: 1rem;
}

.info-container {
  margin-top: 2.5rem;
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.info-container h1 {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 2rem;
  font-weight: 600;
}

.personal-info {
  width: 100%;
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.personal-info div {
  width: 50%;
}

.personal-info input {
  width: 100%;
  padding: 0.8rem;
}

/* Ajout du style pour textarea */
.message-container {
  width: 100%;
}

.message-container textarea {
  width: 100%;
  height: 10rem;
  resize: none;
  vertical-align: top; /* Correction du curseur */
  border-radius: 5px;
}

/* Style du bouton */
.send-btn {
  background-color: black;
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  font-size: 1rem;
  font-weight: 500;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease-in-out;
}

/* Effet au survol */
.send-btn:hover {
  background-color: #333;
}

/* 🔽 Responsive Mode */
@media (max-width: 768px) {
  .info-container {
    width: 90%;
  }

  .personal-info {
    flex-direction: column;
    gap: 0.8rem;
  }

  .personal-info div {
    width: 100%;
  }

  .info-container h1 {
    font-size: 1.8rem;
  }
}

@media (max-width: 480px) {
  .info-container h1 {
    font-size: 1.5rem;
  }
}
